.skills-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

img.skills-wrapper-img {
  padding: 10px;
  margin: 10px;
  justify-self: center;
}

img.skills-wrapper-img:hover {
  background-color: #f1f1f1;
  border-radius: 50%;
  cursor: pointer;
}

.education-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  /* grid-template-rows: 1fr; */
  grid-template-areas: "iitk msrit";
}
img.education-wrapper-img {
  padding: 10px;
  margin: 10px;
  justify-self: center;
}

.education-iitk {
  grid-area: iitk;
}
.education-msrit {
  grid-area: msrit;
}

.education-iitk > img {
  width: 100px;
}

.education-msrit > img {
  width: 115px;
}
.education-degree {
  font-size: 1.25rem;
}
.education-time {
  font-size: 0.95rem;
  color: #525252;
}

.prof-ex-wrapper {
  display: grid;
  grid-template-columns: 0.5fr 3fr;
  grid-template-rows: 1fr;
  grid-row-gap: 10px;
  row-gap: 10px;
  grid-template-areas: "mastercard-logo mastercard";
  /* "anothercompany-logo anothercompany"; */
}
.prof-ex-mastercard-img {
  grid-area: mastercard-logo;
}
.prof-ex-mastercard {
  grid-area: mastercard;
}
.prof-ex-mastercard-img > img {
  width: 150px;
}

img.prof-ex-wrapper-img {
  /* padding: 10px;
  margin: 10px; */
  /* justify-self: center; */
  text-align: center;
}
.prof-ex-content-wrapper {
  margin-left: 50px;
}
.prof-ex-time {
  font-size: 0.95rem;
  color: #525252;
}
